___CSS_LOADER_EXPORT___.push([module.id, `#tasks-container {
  padding: 1em;
  display: grid;
  grid-template-rows: repeat(auto-fill, 3em);
  gap: 1.2em;
}

.task {
  display: flex;
  border-radius: 8px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  background-color: #e4f2e7;
  align-items: center;
  height: 3em;
  width: 100%;
  gap: 15px;
  padding: 0px 10px 3px 10px;
}

.task input[type='checkbox'] {
  appearance: none;
  cursor: pointer;
  width: 30px;
  aspect-ratio: 1;
  border-radius: 8px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  position: relative;
  transition: all 0.2s ease-in-out;
  background-color: white;
}

.task input[type='checkbox']::before {
  font-family: 'Quicksand', sans-serif;
  position: absolute;
  bottom: -4px;
  left: 1px;
  content: '✔';
  font-size: 40px;
  color: #2d3e40;
  transform: scale(0);
  transition: all 0.2s ease-in-out;
}

.task input[type='checkbox']:checked::before {
  animation: zoom 0.5s ease-in-out;
  transform: scale(1);
}

@keyframes zoom {
  0% {
    transform: scale(0);
  }

  20% {
    transform: scale(1);
  }

  40% {
    transform: scale(0.7);
  }

  50% {
    transform: scale(1);
  }

  70% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}
.task > .task-name {
  flex-grow: 3;
}
.task > .due {
  width: 6em;
  font-size: 0.9em;
  font-style: italic;
}

.task input[type='date'] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 18px;
  height: 30px;
  cursor: pointer;
  border: none;
  background-color: transparent;
}

.task input[type='date']:focus {
  border: none;
  outline: none;
}

.delete-task {
  background-color: transparent;
  position: relative;
  border: none;
  cursor: pointer;
}

.icon {
  transform: scale(1.2);
  transition: 0.2s linear;
}

.delete-task:hover > .icon {
  transform: scale(1.5);
}

.delete-task:hover > .icon path {
  fill: rgb(168, 7, 7);
}

.delete-task:hover::after {
  visibility: visible;
  opacity: 1;
  top: -160%;
}

.priority-select {
  -webkit-appearance: none; /* Safari and Chrome */
  -moz-appearance: none; /* Firefox */
  appearance: none;

  border: none;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  background-size: 10px;
  padding: 3px;
}

.priority-input {
  width: 148px;
  border: 1px solid #ccc;
}

.high-priority {
  background-color: #ff9999; /* Light red */
}

.medium-priority {
  background-color: #ffcc99; /* Light orange */
}

.low-priority {
  background-color: #99ff99; /* Light green */
}
